@import '../../common';

.wrapper {
	position: relative; }

.counter {
	margin-top: 3em;
	overflow: auto;
	overflow-scrolling: touch;
	-webkit-mask-image: linear-gradient(90deg, rgba(#000000, 0), #000000 5%, #000000 95%, rgba(#000000, 0)); // @TODO: remove prefix

	&::-webkit-scrollbar {
		width: 0;
		height: 0;
		background: transparent; }

	&In {
		display: flex;

		&::before,
		&::after {
			content: '';
			width: 25%;
			display: block;
			flex-shrink: 0; } } }

.background {
	content: '';
	position: absolute;
	left: 0;
	right: 0;
	bottom: 0;
	top: 0;
	display: flex;
	flex-direction: column;

	&::before {
		content: '';
		width: 100%;
		padding-top: 100% / $product-image-aspect-ratio / 2; }

	&::after {
		content: '';
		width: 100%;
		margin-top: $product-info-top-space;
		flex-grow: 1;
		background-color: #F5F5F5; } }



