.wrapper {
  width: 50%;
  flex-shrink: 0;
  color: #DDDDDD;
  transition: color .1s;
}

.image {
  position: relative;
  margin: 0 auto;
  max-width: 50vh;
  overflow: hidden;
}

.imageFrame {
  position: relative;
}

.imageFrame::before {
  content: '';
  display: block;
  padding-top: 75%;
}

.imageIn {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: contain;
  object-position: center bottom;
  transition: opacity .3s;
  opacity: 0;
}

.isRealVisible .imageIn.viewReal,
.isPlaceholderVisible .imageIn.viewPlaceholder {
  opacity: 1;
}

.info {
  position: relative;
  margin-top: 1em;
  padding: 2em 1em 1em;
  color: #000000;
}

.info::before,
.info::after {
  content: '';
  display: block;
  background-color: inherit;
  position: absolute;
  top: 0;
  bottom: 0;
  width: 50%;
  left: 100%;
}

.info::after {
  left: auto;
  right: 100%;
}

.infoIn {
  position: relative;
  z-index: 1;
  display: flex;
  flex-wrap: wrap;
}

.viewShadow .name::before,
.viewShadow .price::before,
.viewShadow .note::before {
  content: '';
  height: .8em;
  background-color: currentColor;
  border-radius: .1em;
  display: inline-block;
  max-width: 100%;
}

.name {
  margin: 0 .5em 0 0;
}

.name::before {
  width: 6em;
  opacity: .22;
}

.price {
  margin: 0;
  position: relative;
  top: .1em;
}

.price::before {
  width: 4em;
  opacity: .1;
}

.note {
  width: 100%;
  margin: .5em 0 0;
}

.note::before {
  width: 15em;
  opacity: .1;
}

.addToCart {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  opacity: 0;
  z-index: 1;
}




