.wrapper {
  position: relative;
}

.counter {
  margin-top: 3em;
  overflow: auto;
  overflow-scrolling: touch;
  -webkit-mask-image: linear-gradient(90deg, rgba(0, 0, 0, 0), #000000 5%, #000000 95%, rgba(0, 0, 0, 0));
}

.counter::-webkit-scrollbar {
  width: 0;
  height: 0;
  background: transparent;
}

.counterIn {
  display: flex;
}

.counterIn::before,
.counterIn::after {
  content: '';
  width: 25%;
  display: block;
  flex-shrink: 0;
}

.background {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  display: flex;
  flex-direction: column;
}

.background::before {
  content: '';
  width: 100%;
  padding-top: 37.5%;
}

.background::after {
  content: '';
  width: 100%;
  margin-top: 1em;
  flex-grow: 1;
  background-color: #F5F5F5;
}




