.wrapper {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: #FFFFFF;
  border-radius: 1em;
  padding: 1em;
  transform: translateY(100%);
  opacity: 0;
  visibility: hidden;
  transition-property: opacity, transform, visibility;
  transition-duration: .3s;
}

.wrapper.isActive {
  transform: none;
  opacity: 1;
  visibility: visible;
}




